<template>
    <div>
        <!-- 此组件内容有待进一步完善 -->
        <el-dropdown>
            <span class="el-dropdown-link">
                <el-avatar icon="fa fa-user-circle-o fa-lg" class="m-2" style="color:red;"></el-avatar>
            </span>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>个人信息</el-dropdown-item>
                <el-dropdown-item>修改密码</el-dropdown-item>
                <!-- el-dropdown-item 的单击事件要加上 native 修饰符才可以 -->
                <el-dropdown-item @click.native="logout">注销</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import AccountService from "../../api/service/account_service.js"
export default {
    name: 'UserInfo',
    data: () => ({
        //
    }),
    methods: {
        ...mapMutations(
            // 把 状态中定义的方法名："SET_DRAWER" 映射为当前组件的方法： setDrawer，
            ["SET_CURRENT_USER"]
        ),
        logout() {
            console.log("logout");
            AccountService.logOut();
            if (this.$route.path != "/") {
                this.SET_CURRENT_USER(null);    // 清空 vuex 状态中的用户信息
                localStorage.removeItem("preRoute");  // 删除已保存的前一页地址
                localStorage.removeItem("token");   // 删除已保存的用户 token
                this.$router.push("/");
            }
        },

    },

}
</script>

<style>
.el-dropdown-link {
    cursor: pointer;
}
.m2 {
    margin: 8px;
}
</style>